import { Router } from '@angular/router';
import { LoginComponent } from './../login/login.component';
import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { mimeType } from '../mime';
import * as CoursesType from '../enum';
import { MatDialog } from '@angular/material/dialog';
import { UtilService } from 'src/app/util/util.service';
import { AuthService } from '../auth.service';
import { SocialMediaTerms } from '../enum';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {
  hide = true;
  signVisible = true;
  signupSuccess = false;
  signupStatusListener: Subscription;

  imagePreview: string;
  form: FormGroup;
  isVerified = false;
  branches = CoursesType.Branch;
  ShortBranch = CoursesType.BranchShortForm;
  courses = CoursesType.Course;
  sm: typeof SocialMediaTerms;
  constructor(public matDialog: MatDialog, private authService: AuthService, private utilService: UtilService, public router: Router) { }

  ngOnInit(): void {
    this.utilService.ScrollToTop();
    this.sm = SocialMediaTerms;
    this.form = new FormGroup({
      // image: new FormControl(null, {validators: [Validators.required], asyncValidators: [mimeType]}),
      username: new FormControl(null, { validators: [Validators.required] }),
      firstname: new FormControl(null, { validators: [Validators.required] }),
      lastname: new FormControl(null, { validators: [Validators.required] }),
      password: new FormControl(null, { validators: [Validators.required, Validators.minLength(8)] }),
      email: new FormControl(null, { validators: [Validators.required, Validators.email] }),
      contact: new FormControl(null, { validators: [Validators.required, Validators.min(999999999)] }),
      branch: new FormControl(null, { validators: [Validators.required] }),
      course: new FormControl(null, { validators: [Validators.required] }),
      graduationYear: new FormControl(null, { validators: [Validators.required,Validators.min(999)]}),
      college: new FormControl(null, { validators: [Validators.required] }),
      refferalCode: new FormControl(null)
    });
    this.signupSuccess = this.authService.getSignupStatus();
    // this.isLoading = false
    this.utilService.setLoaderStatus(false);
    this.signupStatusListener = this.authService.getSignupStatusListener().subscribe(isSignedUp => {
      // this.isLoading = false,
      this.utilService.setLoaderStatus(false);
      this.signupSuccess = isSignedUp;
    });
  }
  onSignup() {
    if (this.form.invalid) {
      return;
    } else {
      this.authService.createUser(
        // this.form.value.image,
        this.form.value.username,
        this.form.value.firstname,
        this.form.value.lastname,
        this.form.value.password,
        this.form.value.email,
        this.form.value.contact,
        this.form.value.branch,
        this.form.value.course,
        this.form.value.graduationYear,
        this.form.value.college,
        this.form.value.refferalCode
      );
      this.signVisible = false;
      this.utilService.setLoaderStatus(true);
      // this.isLoading = true;
      this.isVerified = true;
    }
  }
  Login() {
    this.matDialog.open(LoginComponent);
    this.signVisible = false;
  }
  onImagePicked(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });
    this.form.get('image').updateValueAndValidity();
    const reader = new FileReader();
    reader.onload = () => {
      this.imagePreview = reader.result as string;
    };
    reader.readAsDataURL(file);
  }
  openSocialMedia(type: string) {
    return this.utilService.openSocialMedia(type);
  }
  getLoaderStatus() {
    return this.utilService.getLoaderStatus();
  }
}
