import { Page, ExternalURLs } from 'src/app/util/page.enum';
import { Injectable, PLATFORM_ID, Inject } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { isPlatformBrowser } from '@angular/common';
import { HttpClient } from '@angular/common/http';
// import * as GLOBAL from '../utils/globals';
@Injectable()
export class UtilService {
  loaderStatus: any;
  constructor(
    private http: HttpClient,
    private route: ActivatedRoute,
    @Inject(PLATFORM_ID) private platformId: Object
  ) { }

  isMobileView() {
    if (isPlatformBrowser(this.platformId)) {
      return window.innerWidth <= 767;
    }
  }
  isBrowserPlatform() {
    return isPlatformBrowser(this.platformId);
  }
  isMobTabViewForNav() {
    if (isPlatformBrowser(this.platformId)) {
      return window.innerWidth >= 768 && window.innerWidth <= 1100;
    }
  }
  isDeskTopViewForNav() {
    if (isPlatformBrowser(this.platformId)) {
      return window.innerWidth >= 1101;
    }
  }
  isMobTabView() {
    if (isPlatformBrowser(this.platformId)) {
      return window.innerWidth >= 768 && window.innerWidth <= 1371;
    }
  }
  isDeskTopView() {
    if (isPlatformBrowser(this.platformId)) {
      return window.innerWidth > 1371;
    }
  }
  ScrollToTop() {
    if (isPlatformBrowser(this.platformId)) {
      window.scrollTo(0, 0);
    }
  }
  ScrollToBottom() {
    if (isPlatformBrowser(this.platformId)) {
      window.scrollTo(0, document.body.scrollHeight);
    }
  }
  openSocialMedia(type: string) {
    this.ScrollToTop();
    const URL = ExternalURLs[type];
    window.open(URL, '_blank');
  }
  getLoaderStatus() {
    return this.loaderStatus;
  }
  setLoaderStatus(status) {
    this.loaderStatus = status;
  }
}
