import { UtilService } from 'src/app/util/util.service';
import { SocialMediaTerms } from './../enum';
import { Router } from '@angular/router';
import { SignupComponent } from './../signup/signup.component';
import { MatDialog } from '@angular/material/dialog';
import { AuthService } from './../auth.service';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Subscription } from 'rxjs';
import { ReqMailComponent } from '../req-mail/req-mail.component';
import { SuccessMsgComponent } from '../success-msg/success-msg.component';
import * as AOS from 'aos';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, OnDestroy {
  hide = true;
  isLoading = false;
  isVerified = false;
  private authStatusSub: Subscription;
  sm: typeof SocialMediaTerms;
  mail = '';
  authStatusListener: Subscription;
  userIsAuthenticated = false;

  constructor(public authService: AuthService, public matDialog: MatDialog, public router: Router, public utilService: UtilService) { }
  ngOnInit(): void {
    AOS.init()
    this.sm = SocialMediaTerms;
    this.authStatusSub = this.authService.getAuthStatusListener().subscribe();
    this.mail = this.authService.getEmail();
    this.userIsAuthenticated = this.authService.getIsAuth();
    this.authStatusListener = this.authService.getAuthStatusListener()
      .subscribe(isAuthenticated =>{
        this.isLoading = false,
        this.userIsAuthenticated = isAuthenticated
        });
        if(this.userIsAuthenticated == true){
          this.matDialog.open(SuccessMsgComponent)
       }
   
  }
  //
  onLogin(form: NgForm) {
    if (form.invalid) {
      return;
    }else{
      this.authService.login(form.value.email, form.value.password);
      this.isVerified = true; 
      this.isLoading = true;
      this.router.navigate(['/home'])
    }
  }
  ngOnDestroy() {
    this.authStatusSub.unsubscribe();
  }
  signup() {
    this.matDialog.open(SignupComponent);
  }
  openSocialMedia(type: string) {
    return this.utilService.openSocialMedia(type);
  }
  forgot(){
    this.matDialog.open(ReqMailComponent);
  }
}
 