import { Component, OnInit } from '@angular/core';
import { SignupComponent } from 'src/app/component/authentication/signup/signup.component';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Page } from 'src/app/util/page.enum';
import { AuthService } from 'src/app/component/authentication/auth.service';
import { Subscription } from 'rxjs';
import { sessionStore } from 'src/app/util/sessionStorageHelper';
import { SessionStoreKey } from 'src/app/util/SessionStoreKey.enum';
import { UtilService } from 'src/app/util/util.service';
import * as AOS from 'aos';

@Component({
  selector: 'app-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.css']
})
export class SliderComponent implements OnInit {
  userIsAuthenticated = false;
  authStatusListener: Subscription;
  constructor(public matDialog: MatDialog,public router: Router, private authService: AuthService,public utilService: UtilService,) { }

  ngOnInit(): void {
    AOS.init()
    this.userIsAuthenticated = this.authService.getIsAuth();
    this.authStatusListener = this.authService.getAuthStatusListener()
    .subscribe(isAuthenticated =>
      this.userIsAuthenticated = isAuthenticated);
       
  }
  signUp() {
    this.matDialog.open(SignupComponent);
  }
  buyNow(courseKey?: string){
    this.utilService.ScrollToTop();
    this.router.navigate([Page.PACKAGE]);
  }
 }
