import { Component, OnInit } from '@angular/core';
import { UtilService } from 'src/app/util/util.service';
import { Page, ExternalURLs, PageRouter } from 'src/app/util/page.enum';
import { Router } from '@angular/router';
import * as course_detail from 'src/app/util/course-detail.enum';
import { SocialMediaTerms } from '../authentication/enum';
import { sessionStore } from 'src/app/util/sessionStorageHelper';
import { SessionStoreKey } from 'src/app/util/SessionStoreKey.enum';
import { AuthService } from './../authentication/auth.service';
import { Subscription } from 'rxjs';
import { MatDialog } from '@angular/material/dialog';
import { DisclaimerComponent } from 'src/app/pageComponent/company/disclaimer/disclaimer.component';
import { TermsOfServiceComponent } from 'src/app/pageComponent/company/terms-of-service/terms-of-service.component';
import { PricingComponent } from 'src/app/pageComponent/pricing/pricing.component';
import { PaymentRefundPolicyComponent } from 'src/app/pageComponent/company/payment-refund-policy/payment-refund-policy.component';
import { PrivacyPolicyComponent } from 'src/app/pageComponent/company/privacy-policy/privacy-policy.component';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {
  Page: typeof Page;
  sm: typeof SocialMediaTerms;
  branch: any;
  userIsAuthenticated = false;
  authStatusListener: Subscription;
  constructor(public router: Router, public utilService: UtilService, public authService: AuthService, public matDialog: MatDialog) { }

  ngOnInit(): void {
    this.Page = Page;
    this.sm = SocialMediaTerms;
    this.userIsAuthenticated = this.authService.getIsAuth();
    this.authStatusListener = this.authService.getAuthStatusListener()
      .subscribe(isAuthenticated =>
        this.userIsAuthenticated = isAuthenticated);
    this.Page = Page;
  }
  openCourseDetails(courseKey?: string) {
    sessionStore.set(SessionStoreKey.BRANCH, courseKey);

    this.utilService.ScrollToTop();
    this.router.navigate([Page.COURSE_DETAILS]);
  }
  getBranchName() {
    this.branch = sessionStore.get(SessionStoreKey.BRANCH);
    return this.branch;
  }
  getBranchDetail() {
    return course_detail.showDetail_Civil;
  }

  goToPage(pageType: string) {
    this.utilService.ScrollToTop();
    this.router.navigate([pageType]);
  }
  goToBlog() {
    this.utilService.ScrollToTop();
    window.open(ExternalURLs.BLOG, '_blank');
  }
  openSocialMedia(type: string) {
    return this.utilService.openSocialMedia(type);
  }
  Disclaimer(){
    this.matDialog.open(DisclaimerComponent);
  }
  Terms(){
    this.matDialog.open(TermsOfServiceComponent);
  }
  Privacy(){
    this.matDialog.open(PrivacyPolicyComponent);
  }
  Payment(){
    this.matDialog.open(PaymentRefundPolicyComponent);
  }
}
