import { AboutComponent } from './pageComponent/about/about.component';
import { ContactComponent } from './pageComponent/contact/contact.component';
import { Page } from 'src/app/util/page.enum';
import { LoginComponent } from './component/authentication/login/login.component';
import { SignupComponent } from './component/authentication/signup/signup.component';
import { VideoPlayerComponent } from './pageComponent/video/video-player/video-player.component';
import { VideosListComponent } from './pageComponent/video/videos-list/videos-list.component';
import { CourseDetailComponent } from './pageComponent/course-detail/course-detail.component';
import {ReportSectionComponent} from './pageComponent/report-section/report-section.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomePageComponent } from './pageComponent/home-page/home-page.component';
import { UtilService } from 'src/app/util/util.service';
import { RoutingGuard } from './component/authentication/route.guards';
import { ErrorMsgComponent } from './component/authentication/error-msg/error-msg.component';
import { ForgotPassComponent } from './component/authentication/forgot-pass/forgot-pass.component';
import { PaymentService } from './component/authentication/payment.service';
import { PackageComponent} from './pageComponent/package/package.component'

const routes: Routes = [
  { path: Page.SIGNUP, component: SignupComponent },
  { path: Page.LOGIN, component: LoginComponent },
  { path: Page.VIDEOS, component: VideosListComponent , canActivate: [RoutingGuard]},
  { path: Page.VIDEO_PLAYER, component: VideoPlayerComponent, canActivate: [RoutingGuard] },
  { path: Page.COURSE_DETAILS, component: CourseDetailComponent },
  { path: 'error', component: ErrorMsgComponent },
  { path: Page.ABOUT, component: AboutComponent },
  { path: Page.CONTACT, component: ContactComponent },
  { path: 'forgot', component: ForgotPassComponent },
  { path: Page.HOME, component: HomePageComponent },
  { path: Page.REPORT, component: ReportSectionComponent },
  { path: Page.PACKAGE, component: PackageComponent },
  { path: '**', component: HomePageComponent }
];
@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  providers: [
    UtilService,
    RoutingGuard,
    PaymentService
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
