import { Router } from '@angular/router';
import { VideosService } from './../videos.service';
import { Component, OnInit } from '@angular/core';
import { MediaObserver, MediaChange } from '@angular/flex-layout';
import { Subscription } from 'rxjs';
import * as CoursesType from '../videos-list.enum';
import { DomSanitizer } from '@angular/platform-browser';
import { Page, ExternalURLs, PageRouter } from 'src/app/util/page.enum';
@Component({
  selector: 'app-video-player',
  templateUrl: './video-player.component.html',
  styleUrls: ['./video-player.component.scss']
})
export class VideoPlayerComponent implements OnInit {
  mediaSub: Subscription;
  deviceXs: boolean;
  deviceMd: boolean;
  deviceSm: boolean;
  popularId ;
  Page: typeof Page;
    link: string;
  safeURL: any;
  thumbNail;

  constructor(public mediaObserver: MediaObserver , private videoService: VideosService, private router: Router,private sanitizer: DomSanitizer) { }

  videoId ='';
  videoTitle= '';
  ngOnInit(): void {
    const tag = document.createElement('script');
    tag.src = 'https://www.youtube.com/iframe_api';
    document.body.appendChild(tag);
    this.mediaSub = this.mediaObserver.media$.subscribe((result: MediaChange) => {
      this.deviceXs = result.mqAlias == 'xs' ? true : false;
      this.deviceMd = result.mqAlias == 'md' ? true : false;
      this.deviceSm = result.mqAlias == 'sm' ? true : false;
    });

    
    this.videoId = localStorage.getItem('videoid');
    this.videoTitle = localStorage.getItem('videotitle')
    // player
    this.link = `https://www.youtube-nocookie.com/embed/${this.videoId}?modestbranding=1&rel=0&showinfo=0`;
    if (this.link) {
      this.safeURL = this.sanitizer.bypassSecurityTrustResourceUrl(this.link);
    }
    this.thumbNail = `http://img.youtube.com/vi/${this.videoId}/0.jpg`;

  }
back(){
this.router.navigate([Page.VIDEOS]);
localStorage.removeItem('videoid');
localStorage.removeItem('videotitle');
}


}
