import { UtilService } from 'src/app/util/util.service';
import { Branch } from './../../component/authentication/enum';
import { Component, OnInit } from '@angular/core';
import { sessionStore } from 'src/app/util/sessionStorageHelper';
import { SessionStoreKey } from 'src/app/util/SessionStoreKey.enum';
import * as course_detail from 'src/app/util/course-detail.enum';
import { ExternalURLs } from 'src/app/util/page.enum';
import { MatDialog } from '@angular/material/dialog';
import { PaymentComponent } from 'src/app/component/authentication/payment/payment.component';


@Component({
  selector: 'app-course-detail',
  templateUrl: './course-detail.component.html',
  styleUrls: ['./course-detail.component.css']
})
export class CourseDetailComponent implements OnInit {
  branch: any;
  backgroundImageClass: string;
  points;


  constructor(private utilService: UtilService, public matDialog: MatDialog,) { }

  ngOnInit(): void {
  }
  getBranchName() {
    this.branch = sessionStore.get(SessionStoreKey.BRANCH);
    return this.branch;
  }
  getBranchClassName() {
    this.backgroundImageClass = sessionStore.get(SessionStoreKey.BRANCH_CLASS);
    return this.backgroundImageClass;
  }
  getBranchDetail() {
    if (this.branch === 'Civil Engineering') {
      return course_detail.showDetail_Civil;
    }
    else if (this.branch === 'Electrical Engineering') {
      this.points = course_detail.showDetail_Electrical.extraPoints;
      return course_detail.showDetail_Electrical;
    }
    else if (this.branch === 'Chemical Engineering') {
      this.points = course_detail.showDetail_Chemical.extraPoints;

      return course_detail.showDetail_Chemical;

    }
    else if (this.branch === 'Mechanical Engineering') {
      this.points = course_detail.showDetail_Mechanical.extraPoints;
      return course_detail.showDetail_Mechanical;
    }
    else if (this.branch === 'Metallurgy Engineering') {
      this.points = course_detail.showDetail_Metallurgy.extraPoints;
      return course_detail.showDetail_Metallurgy;
    }
    else if (this.branch === 'Biotech Engineering') {
      return course_detail.showDetail_Biotech;
    }
      else if (this.branch === 'Electronics & Communication Engineering') {
      return course_detail.showDetail_ECE;
    }
      else if (this.branch === 'Aeronautical Engineering') {
      return course_detail.showDetail_Aeronautical;
    }
  }
}
