import { VideosService } from './../../pageComponent/video/videos.service';
import { User } from './interface';
import { Injectable, ɵConsole } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { Router } from '@angular/router';
import { environment } from './../../../environments/environment';
import { Token } from '@angular/compiler/src/ml_parser/lexer';
const BACKEND_URL = environment.Apiurl + "/users";
const PAYMENT_URL = environment.Apiurl + '/payment/';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private token: string;
  private isAuthenticated = false;
  private userId: string;
  private mailSent = false;
  private mailTokemTimer: any;
  private passwordChangeTimer: any;
  private changePassword = false;
  private passwordChangeStatus = new Subject<boolean>();
  private mailStatusListener = new Subject<boolean>();
  private AuthStatusListener = new Subject<boolean>();
  private signupStatusListener = new Subject<boolean>();
  private signupStatus = false;
  private tokenTimer: any;
  private accessTokenTimer: any;
  name: string;
  email = '';
  private purchasedBranch: string;
  private username: string;
  private packageDetails = {
    pkgName: '',
    pkgAmount: 0
  };
  private firstname: string;
  private lastname: string;
  private Email: string;
  private course: string;
  private profileBranch: string;
  private contactNumber: number;
  private signature: string;
  private paymentId: string;
  private refferalCode:string;
  message: string;
  constructor(private http: HttpClient, private router: Router, private videoService: VideosService) { }

  // signup status
  getSignupStatus() {
    return this.signupStatus;
  }
  getSignupStatusListener() {
    return this.signupStatusListener.asObservable();
  }
  // forgot mai sent 

  getMailSent() {
    return this.mailSent;
  }
  getMailStatusListener() {
    return this.mailStatusListener.asObservable();
  }
  // change Password 

  getChangePassword() {
    return this.changePassword;
  }
  getPassChangeStatus() {
    return this.passwordChangeStatus.asObservable();
  }
  // profile data
  getName() {
    return this.name;
  }
  getReport() {
    return this.http.get(BACKEND_URL + '/getreport');
  }
  Username() {
    return this.username;
  }
  RefferalCode(){
    return this.refferalCode;
  }
  setPackageDetails(pkgObject,cost) {
    this.packageDetails.pkgName = pkgObject.pkgName;
    this.packageDetails.pkgAmount = cost;
  }
  getPackageDetails() {
    return this.packageDetails;
  }
  Firstname() {
    return this.firstname;
  }
  getResponseMsg() {
    return this.message;
  }
  Lastname() {
    return this.lastname;
  }
  Gmail() {
    return this.Email;
  }
  Course() {
    return this.course;
  }
  userBranch() {
    return this.profileBranch;
  }
  userContactNumber() {
    return this.contactNumber;
  }
  userSignature() {
    return this.signature;
  }
  userPaymentId() {
    return this.paymentId;
  }
  getToken() {
    return this.token;
  }
  getIsAuth() {
    return this.isAuthenticated;
  }
  getUserId() {
    return this.userId;
  }
  getAuthStatusListener() {
    return this.AuthStatusListener.asObservable();
  }
  getEmail() {
    return this.purchasedBranch;
  }
  getBranchValue() {
    return this.getBranch().purchasedBranch;
  }
  // signup

  // tslint:disable-next-line: max-line-length
  createUser(username: string, firstname: string, lastname: string, password: string, email: string, contact: string, branch: string, course: string, graduationYear: string, college: string, refferalCode: string) {
    const authData = new FormData();

    //authData.append("image",image,username);
    authData.append('username', username);
    authData.append('firstname', firstname);
    authData.append('lastname', lastname);
    authData.append('email', email);
    authData.append('password', password);
    authData.append('contact', contact);
    authData.append('branch', branch);
    authData.append('course', course);
    authData.append('graduationYear', graduationYear);
    authData.append('college', college);
    authData.append('refferalCode', refferalCode);

    return this.http.post<{ message: string, accessToken: string, refreshToken: string, userId: string }>(BACKEND_URL + '/signup', authData).subscribe(response => {
      if (response.accessToken) {
        this.signupStatus = true;
        this.signupStatusListener.next(true);
        this.accessTokenTimer = setTimeout(() => {
          this.signupStatus = false;
        }, 3600);
      }
    });
  }

  // login
  login(email: string, password: string) {
    const authData = { email, password };
    this.http.post<{
      message: string, token: string, expiresIn: number, userId: string, email: string, firstname: string, lastname: string,refferalCode:string,
      username: string, branch: string, course: string, contact: number, paymentId: string, signature: string
    }>(BACKEND_URL + '/login', authData)
      .subscribe(response => {
        console.log(response)
        const token = response.token;
        this.token = token;
        this.message = response.message;
        if (token) {
          const expiresInDuration = response.expiresIn;
          this.setAuthTimer(expiresInDuration);
          this.isAuthenticated = true;
          this.userId = response.userId;
          this.Email = response.email;
          this.paymentDetails(response.email);
          this.firstname = response.firstname;
          this.lastname = response.lastname;
          this.username = response.username;
          this.course = response.course;
          this.profileBranch = response.branch;
          this.refferalCode = response.refferalCode,
          this.contactNumber = response.contact;
          this.signature = response.signature;
          this.paymentId = response.paymentId;
          this.AuthStatusListener.next(true);
          const now = new Date();
          const expirationDate = new Date(now.getTime() + expiresInDuration * 1000);
          this.saveAuthData(token, expirationDate, this.userId, this.Email, this.firstname, this.lastname, this.username,
            this.profileBranch, this.contactNumber, this.signature, this.paymentId, this.course, this.refferalCode);
          this.router.navigate(['/']);
          // console.log(this.isAuthenticated);
        }
      }, error => {
        this.AuthStatusListener.next(false);
      });
  }

  // req forgot password link to mail

  reqMail(email: string) {
    const data = { email };
    return this.http.put<{ message: string, success: boolean }>(BACKEND_URL + "/resetPass", data).subscribe(response => {
      if (response.success == true) {
        this.mailSent = true;
        this.mailStatusListener.next(true);
        this.mailTokemTimer = setTimeout(() => {
          this.mailSent = false;
        }, 3600)
      }
      this.router.navigate(['/'])
    });
  }


  // forgot password 

  forgotPassword(password: string, id: string) {
    const data = { password, id };
    this.http.put<{ message: string, success: true }>(BACKEND_URL + "/resetPassword", data).subscribe(response => {
      if (response.success == true) {
        this.changePassword = true;
        this.passwordChangeStatus.next(true);
        this.passwordChangeTimer = setTimeout(() => {
          this.changePassword = false;
        }, 3600);
      }
    });
  }




  autoAuthUser() {
    const authInformation = this.getAuthData();
    const branchInformation = this.getBranch();
    if (!authInformation) {
      return;
    }
    const now = new Date();
    const expiresIn = authInformation.expirationDate.getTime() - now.getTime();
    if (expiresIn > 0) {
      this.token = authInformation.token;
      this.isAuthenticated = true;
      this.userId = authInformation.userId;
      this.Email = authInformation.email;
      this.firstname = authInformation.firstname;
      this.lastname = authInformation.lastname;
      this.username = authInformation.username;
      this.course = authInformation.course;
      this.profileBranch = authInformation.profileBranch;
      this.contactNumber = authInformation.contactNumber;
      this.signature = authInformation.signature;
      this.paymentId = authInformation.paymentId;
      this.purchasedBranch = branchInformation.purchasedBranch;
      this.refferalCode = authInformation.refferalCode;
      this.setAuthTimer(expiresIn / 1000);
      this.AuthStatusListener.next(true);
    }
  }
  logout() {
    this.token = null;
    this.userId = null;
    this.Email = null;
    this.purchasedBranch = null;
    this.name = null;
    this.isAuthenticated = false;
    this.AuthStatusListener.next(false);
    clearTimeout(this.tokenTimer);
    this.clearBranchData();
    this.clearAuthData();
    this.router.navigate(['/']);
    sessionStorage.removeItem('id');
    sessionStorage.removeItem('payment');
    sessionStorage.removeItem('signature');
    localStorage.removeItem('basicCost');
    localStorage.removeItem('premiumCost');
    localStorage.removeItem('premiumCost');
    localStorage.removeItem('basicCost');
    // sessionStorage.removeItem('isLoadedOnce');
  }
  // fetching pament details
  paymentDetails(email: string) {
    return this.http.get<{
      message: string, email: any, branch: any, transactionId: string, expirationTime: number
    }>(PAYMENT_URL + email).subscribe(packagesData => {

      // console.log(packagesData);
      const now = new Date();
      const expiresInDuration = packagesData.expirationTime;
      const expirationDate = new Date(now.getTime() + expiresInDuration * 1000);
      const purchasedbranch = packagesData.branch;
      this.saveBranchData(purchasedbranch, expirationDate);
      // console.log(packagesData.branch);
      this.setBranchTimer(expiresInDuration);

    });
  }

  private setBranchTimer(duration: number) {
    this.tokenTimer = setTimeout(() => {
      this.logout();
    }, duration * 1000);
  }
  private saveBranchData(purchasedBranch: string, expirationDate: Date) {
    localStorage.setItem('purchasedBranch', purchasedBranch);
    localStorage.setItem('expiration', expirationDate.toString());
  }
  private clearBranchData() {
    localStorage.removeItem('purchasedBranch');
    localStorage.removeItem('expiration');
  }
  private getBranch() {
    const purchasedBranch = localStorage.getItem('purchasedBranch');
    const expirationDate = localStorage.getItem('expiration');
    return { purchasedBranch, expirationDate: new Date(expirationDate) };
  }


  private setAuthTimer(duration: number) {

    this.tokenTimer = setTimeout(() => {
      this.logout();
    }, duration * 1000);
  }
  private saveAuthData(token: string,
    expirationDate: Date,
    userId: string,
    email: string,
    firstname: string,
    lastname: string,
    username: string,
    profileBranch: string,
    contactNumber: number,
    signature: string,
    paymentId: string,
    course: string, refferalCode:string) {
    localStorage.setItem('token', token);
    localStorage.setItem('expiration', expirationDate.toString());
    localStorage.setItem('userId', userId);
    localStorage.setItem('email', email);
    localStorage.setItem('firstname', firstname);
    localStorage.setItem('lastname', lastname);
    localStorage.setItem('username', username);
    localStorage.setItem('profileBranch', profileBranch);
    localStorage.setItem('contactNumber', contactNumber.toString());
    localStorage.setItem('signature', signature);
    localStorage.setItem('paymentId', paymentId);
    localStorage.setItem('course', course);
    localStorage.setItem('refferalCode', refferalCode);
  }
  private clearAuthData() {
    localStorage.removeItem('token');
    localStorage.removeItem('expiration');
    localStorage.removeItem('userId');
    localStorage.removeItem('email');
    localStorage.removeItem('firstname');
    localStorage.removeItem('lastname');
    localStorage.removeItem('username');
    localStorage.removeItem('profileBranch');
    localStorage.removeItem('contactNumber');
    localStorage.removeItem('signature');
    localStorage.removeItem('paymentId');
    localStorage.removeItem('course');
  }
  private getAuthData() {
    const token = localStorage.getItem('token');
    const expirationDate = localStorage.getItem('expiration');
    const userId = localStorage.getItem('userId');
    const email = localStorage.getItem('email');
    const firstname = localStorage.getItem('firstname');
    const lastname = localStorage.getItem('lastname');
    const username = localStorage.getItem('username');
    const profileBranch = localStorage.getItem('profileBranch');
    const refferalCode = localStorage.getItem('refferalCode');
    const contactNumber = Number(localStorage.getItem('contactNumber'));
    const signature = localStorage.getItem('signature');
    const paymentId = localStorage.getItem('paymentId');
    const course = localStorage.getItem('course');
    if (!token || !expirationDate) {
      return;
    }
    return {
      token,
      expirationDate: new Date(expirationDate),
      userId, email,
      firstname, lastname, username, profileBranch, contactNumber, signature, paymentId, course,refferalCode
    };
  }
  // contact us
contactUs(name,email,subject, message){
  const values = {name, email, subject, message};
   return this.http.post<{message: string,success: boolean }>(BACKEND_URL + '/contactus', values);
}

  http_post(url, body) {
    return this.http.post<any>(PAYMENT_URL + url, body);
  }
  setPayment(paymentObject) {
    // console.log(_id, payment, signature);
    // const values = {
    //   _id: _id, payment: payment, signature: signature
    // };
    return this.http.post<{message: string, success: boolean}>(PAYMENT_URL + 'payment', paymentObject);
  }
  http_delete(url, body) {
    // console.log(body);
    return this.http.delete<any>(PAYMENT_URL + url, body);
  }
}
