import { UtilService } from './../../util/util.service';
import { Component, OnInit } from '@angular/core';
import { SocialMediaTerms } from 'src/app/component/authentication/enum';
import { NgForm } from '@angular/forms';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { AuthService } from 'src/app/component/authentication/auth.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  sm: typeof SocialMediaTerms;
  msg: string;

  constructor(private utilService : UtilService, private authService: AuthService) { }

  ngOnInit(): void {
    this.sm = SocialMediaTerms;
  
   
  }
  openSocialMedia(type: string) {
    return this.utilService.openSocialMedia(type);
  }
  Submit(form: NgForm){
    if (form.invalid) {
      return;
    }else{
    this.authService.contactUs(
      form.value.name,
      form.value.email,
      form.value.subject,
      form.value.message
    ).subscribe(response => {
      if(response.success == true){
        this.msg = "Our team will contact you Soon "
      }
    })
    form.reset();
  
  }

}}
