import { Router } from '@angular/router';
import { AuthService } from './../authentication/auth.service';
import { HeadermenuComponent } from './../headermenu/headermenu.component';
import { LoginComponent } from './../authentication/login/login.component';
import { SignupComponent } from './../authentication/signup/signup.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MediaObserver, MediaChange } from '@angular/flex-layout';
import { Subscription } from 'rxjs';
import * as branchType from 'src/app/component/authentication/enum';
import { sessionStore } from 'src/app/util/sessionStorageHelper';
import { SessionStoreKey } from 'src/app/util/SessionStoreKey.enum';
import { UtilService } from 'src/app/util/util.service';
import { Page, ExternalURLs, PageRouter } from 'src/app/util/page.enum';
import {report} from '../../pageComponent/report-section/report.enum';
import { PaymentComponent } from '../authentication/payment/payment.component';
import { ProfileComponent } from '../authentication/profile/profile.component';
import { SuccessMsgComponent } from '../authentication/success-msg/success-msg.component';
import { LogoutMsgComponent } from '../authentication/logout-msg/logout-msg.component';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  mediaSub: Subscription;
  deviceLg: boolean;
  deviceMd: boolean;
  firstname: string;
  lastName: string;
  branches = branchType.Branch;
  Page: typeof Page;

  userIsAuthenticated = false;
  authStatusListener: Subscription;
  fullName: string;
  constructor(public matDialog: MatDialog, public mediaObserver: MediaObserver,
              public utilService: UtilService, public authService: AuthService, public router: Router) { }
  ngOnInit(): void {
    this.firstname = this.authService.Firstname();
    this.userIsAuthenticated = this.authService.getIsAuth();
    this.authStatusListener = this.authService.getAuthStatusListener()
    .subscribe(isAuthenticated =>
      this.userIsAuthenticated = isAuthenticated);

    this.Page = Page;
    // tslint:disable-next-line: deprecation
    this.mediaSub = this.mediaObserver.media$.subscribe((result: MediaChange) => {
      this.deviceLg = result.mqAlias === 'lg' ? true : false;
      this.deviceMd = result.mqAlias === 'md' ? true : false;
    });
    if (this.authService.Firstname()) {
      this.firstname = this.authService.Firstname().toUpperCase();
    }
    if (this.authService.Lastname()) {
      this.lastName = this.authService.Lastname().toUpperCase();
    }
  }

  Payment() {
    this.matDialog.open(PaymentComponent);
  }
  Signup() {
    this.matDialog.open(SignupComponent);
  }

  Login() {
    this.matDialog.open(LoginComponent);
  }

  logout() {
    this.authService.logout();
    this.matDialog.open(LogoutMsgComponent);
  }

  openDialog() {
    this.matDialog.open(HeadermenuComponent);
    report.isMenuOpened = true;
  }

  openCourseDetails(courseKey?: string) {
    this.utilService.ScrollToTop();
    sessionStore.set(SessionStoreKey.BRANCH, courseKey);
    let branchClass = courseKey.replace(/\s+/g, '-').toLowerCase();
    branchClass = branchClass === 'electronics-&-communication-engineering' ? 'ece' : branchClass;
    sessionStore.set(SessionStoreKey.BRANCH_CLASS, branchClass);
    this.router.navigate([Page.COURSE_DETAILS]);
  }
  goToPage(pageType: string) {
    this.utilService.ScrollToTop();
    this.router.navigate([pageType]);
    report.videoEnable = true;
  }
  goToBlog() {
    this.utilService.ScrollToTop();
    window.open(ExternalURLs.BLOG, '_blank');
  }
  goToJoinUs() {
    this.utilService.ScrollToTop();
    window.open(ExternalURLs.JOINUS, '_blank');
  }
  goToExpertPage() {
    this.utilService.ScrollToTop();
    window.open(ExternalURLs.EXPERT, '_blank');
  }
  profile(){
    this.matDialog.open(ProfileComponent);
  }
  videos(){
    this.router.navigate([Page.VIDEOS]);
  }
}
