import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { VideoI } from './model';





import { environment } from './../../../environments/environment';
const BACKEND_URL = environment.Apiurl + '/videoId';


@Injectable({
  providedIn: 'root'
})
export class VideosService {
videoid = '';
videoTitle = '';
id: VideoI[] = [];
 branch: string ;
  tokenTimer: any;

  constructor(private http: HttpClient) { }
videoId(value){
 this.videoid = value;
 localStorage.setItem('videoid', value);
}
getBranchName(){
  return this.branch;
}
SelectedVideoTitle(title){
  this.videoTitle = title;
  localStorage.setItem('videotitle', title)
}
getTitle(){
  return this.videoTitle;
}
getTimer(){
  return this.tokenTimer;
}
getVideoId(){
  return this.videoid;
}
getCivil(){
  return this.http.get<{message: string, user: any
  }>(BACKEND_URL + '/getCivil');
}
getDemo(){
  return this.http.get<{message: string, user: any
  }>(BACKEND_URL + '/getDemo');
}
getMech(){
  return this.http.get<{message: string, user: any
  }>(BACKEND_URL + '/getMechanical');
}
getElectrical(){
  return this.http.get<{message: string, user: any
  }>(BACKEND_URL + '/getElectrical');
}
getChemical(){
  return this.http.get<{message: string, user: any
  }>(BACKEND_URL + '/getChemical');
}
getMetallurgy(){
  return this.http.get<{message: string, user: any
  }>(BACKEND_URL + '/getMetallurgy');
}

}
