import { Component, OnInit } from '@angular/core';
import { sessionStore } from '../../util/sessionStorageHelper';
import { SessionStoreKey } from '../../util/SessionStoreKey.enum';
import * as branchType from 'src/app/component/authentication/enum';
import { Page } from 'src/app/util/page.enum';
import { Router } from '@angular/router';
import { UtilService } from 'src/app/util/util.service';

@Component({
  selector: 'app-course-component',
  templateUrl: './course-component.component.html',
  styleUrls: ['./course-component.component.css']
})
export class CourseComponentComponent implements OnInit {
  branches = branchType.BranchKey;
  constructor(private router: Router, private utilService: UtilService) { }

  ngOnInit(): void {
  }
  openCourseDetails(courseKey?: string) {
    this.utilService.ScrollToTop();
    sessionStore.set(SessionStoreKey.BRANCH, courseKey);
    let branchClass = courseKey.replace(/\s+/g, '-').toLowerCase();
    branchClass = branchClass === 'electronics-&-communication-engineering' ? 'ece' : branchClass;
    sessionStore.set(SessionStoreKey.BRANCH_CLASS, branchClass);
    this.router.navigate([Page.COURSE_DETAILS]);
  }

}
